import PropTypes from 'prop-types';
import React from 'react';

// material-ui
import { makeStyles, useTheme } from '@material-ui/styles';
import { Stack, Button, Card, CardContent, Grid, Typography, useMediaQuery } from '@material-ui/core';
import useAuth from 'hooks/useAuth'

import DoneAllTwoToneIcon from '@material-ui/icons/DoneAllTwoTone';

// style constant
const useStyles = makeStyles((theme) => ({
    revenueCard: {
        position: 'relative',
        color: '#fff'
    },
    revenueIcon: {
        position: 'absolute',
        right: '13px',
        top: '14px',
        color: '#f68e1f',
        '&> svg': {
            width: '100px',
            height: '100px',
            opacity: '1'
        },
        [theme.breakpoints.down('xs')]: {
            top: '13px',
            '&> svg': {
                width: '80px',
                height: '80px'
            }
        }
    }
}));

// =============================|| REVENUE CARD ||============================= //

const RevenueCard = ({ primary, secondary, content, iconPrimary, color,id, is_approved, date_created }) => {
    const classes = useStyles();
    let { runStoreName, runProductUserFilterValue, url } = useAuth()

    const theme = useTheme();
    const matchDownXs = useMediaQuery(theme.breakpoints.down('xs'));

    let store = primary

    const temp = () => {
        console.log("This is Temp")
    }



    const IconPrimary = iconPrimary;
    const primaryIcon = iconPrimary ? <IconPrimary fontSize="large" /> : null;

    return (
        <Card style={{ background: color }} className={classes.revenueCard} onClick={()=> {runStoreName(id, primary)}}>
            <CardContent>
                <Typography variant="body2" className={classes.revenueIcon}>
                    {primaryIcon}
                </Typography>
                <Grid container direction={matchDownXs ? 'column' : 'row'} spacing={1}>
                    <Grid item xs={12}>
                        <Stack direction="row" spacing={2}>
                        <Typography variant="h3" color="#f68e1f">
                            {primary} 
                        </Typography>
                        { is_approved && <Typography variant="h3" color="#f68e1f">
                            <DoneAllTwoToneIcon/>
                        </Typography>}
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        {content === 0 ? 
                            <Typography variant="h5" color="inherit">
                                No Products
                            </Typography>:
                        content == 1 ?
                            <Typography variant="h5" color="inherit">
                                {secondary} Product
                            </Typography>
                            :
                            <Typography variant="h5" color="inherit">
                                {secondary} Products
                            </Typography>

                        }
                    </Grid>
                    <Grid item xs={12}>
                        {content === 0 ? <Typography variant="h5" color="inherit">
                                No Services
                            </Typography>:
                        content == 1 ?
                            <Typography variant="h5" color="inherit">
                                {content} Service
                            </Typography>
                            :
                            <Typography variant="h5" color="inherit">
                                {content} Services
                            </Typography>

                        }
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="subtitle2" color="inherit">
                            Created {date_created}
                        </Typography>
                    </Grid>
                </Grid>
            </CardContent>
        </Card>
    );
};

RevenueCard.propTypes = {
    primary: PropTypes.string,
    secondary: PropTypes.string,
    content: PropTypes.string,
    iconPrimary: PropTypes.object,
    color: PropTypes.string
};

export default RevenueCard;
